// frontend/src/components/services/ServiceCard.tsx
import { ArrowRight } from 'lucide-react';
import Link from 'next/link';
import { motion } from 'framer-motion';
import { cn } from '@/lib/utils/cn';

interface ServiceCardProps {
  service: {
    id: string;
    title: string;
    slug: string;
    description: string;
    icon: string;
    color: string;
    features?: string[];
    stats?: Array<{ value: string; label: string }>;
  };
  variant?: 'default' | 'compact' | 'featured';
}

const ServiceCard = ({ service, variant = 'default' }: ServiceCardProps) => {
  // Mapping des couleurs aux classes Tailwind
  const colorClasses: Record<string, string> = {
    blue: 'bg-blue-50 text-blue-700 border-blue-200 hover:bg-blue-100',
    purple: 'bg-purple-50 text-purple-700 border-purple-200 hover:bg-purple-100',
    green: 'bg-green-50 text-green-700 border-green-200 hover:bg-green-100',
    orange: 'bg-orange-50 text-orange-700 border-orange-200 hover:bg-orange-100',
    indigo: 'bg-indigo-50 text-indigo-700 border-indigo-200 hover:bg-indigo-100',
    red: 'bg-red-50 text-red-700 border-red-200 hover:bg-red-100'
  };
  
  const colorClass = colorClasses[service.color] || colorClasses.blue;
  
  const link = `/services/${service.slug}`;
  
  if (variant === 'compact') {
    return (
      <motion.div
        whileHover={{ scale: 1.05, translateY: -4 }}
        transition={{ type: "spring", stiffness: 300, damping: 10 }}
      >
        <Link 
          href={link}
          className="block group"
        >
          <div className="bg-white rounded-xl border border-gray-200 p-6 hover:shadow-lg transition-all duration-300 hover:border-primary-200">
            <div className="flex items-start gap-4">
              <motion.div
                whileHover={{ scale: 1.1, rotate: 10 }}
                className={cn(
                  "w-12 h-12 rounded-lg flex items-center justify-center text-2xl flex-shrink-0",
                  colorClass.split(' ')[0],
                  colorClass.split(' ')[1]
                )}
              >
                {service.icon}
              </motion.div>
              <div>
                <h3 className="font-semibold text-gray-900 group-hover:text-primary-600 transition-colors mb-2">
                  {service.title}
                </h3>
                <p className="text-sm text-gray-600 line-clamp-2">
                  {service.description}
                </p>
              </div>
            </div>
          </div>
        </Link>
      </motion.div>
    );
  }
  
  if (variant === 'featured') {
    return (
      <motion.div
        whileHover={{ scale: 1.02, translateY: -8 }}
        transition={{ type: "spring", stiffness: 300, damping: 15 }}
        className="group"
      >
        <div className="relative">
          <motion.div
            className="absolute -inset-4 bg-gradient-to-r from-primary-500 to-secondary-500 rounded-2xl blur opacity-0 group-hover:opacity-30 transition-opacity duration-500"
            animate={{ opacity: [0, 0.3, 0] }}
            transition={{ duration: 3, repeat: Infinity }}
          />
          <div className="relative bg-white rounded-2xl border border-gray-200 overflow-hidden h-full shadow-lg">
            {/* Icon Header */}
            <motion.div
              className={cn(
                "p-6 border-b bg-gradient-to-br",
                colorClass.includes('blue') && "from-blue-50 to-blue-100",
                colorClass.includes('purple') && "from-purple-50 to-purple-100"
              )}
              whileHover={{ scale: 1.05 }}
            >
              <div className="flex items-center justify-between">
                <motion.div 
                  className="text-3xl"
                  whileHover={{ scale: 1.2, rotate: 15 }}
                >
                  {service.icon}
                </motion.div>
                <motion.div
                  animate={{ x: [0, 5, 0] }}
                  transition={{ duration: 2, repeat: Infinity }}
                >
                  <ArrowRight className="w-5 h-5 text-primary-600" />
                </motion.div>
              </div>
            </motion.div>
            
            {/* Content */}
            <div className="p-6">
              <h3 className="text-xl font-bold text-gray-900 mb-3 group-hover:text-primary-600 transition-colors">
                {service.title}
              </h3>
              <p className="text-gray-600 mb-6">
                {service.description}
              </p>
              
              {service.features && service.features.length > 0 && (
                <ul className="space-y-2 mb-6">
                  {service.features.slice(0, 3).map((feature, index) => (
                    <motion.li
                      key={index}
                      className="flex items-center text-sm text-gray-700"
                      initial={{ opacity: 0, x: -20 }}
                      whileInView={{ opacity: 1, x: 0 }}
                      transition={{ delay: index * 0.1 }}
                    >
                      <motion.div 
                        className="w-1.5 h-1.5 rounded-full bg-green-500 mr-2"
                        whileHover={{ scale: 1.5 }}
                      />
                      {feature}
                    </motion.li>
                  ))}
                </ul>
              )}
              
              {service.stats && (
                <div className="grid grid-cols-3 gap-4 mt-6 pt-6 border-t border-gray-100">
                  {service.stats.map((stat, index) => (
                    <motion.div
                      key={index}
                      className="text-center"
                      whileHover={{ scale: 1.1 }}
                    >
                      <div className="text-lg font-bold text-gray-900">{stat.value}</div>
                      <div className="text-xs text-gray-500">{stat.label}</div>
                    </motion.div>
                  ))}
                </div>
              )}
            </div>
            
            {/* Footer */}
            <div className="px-6 pb-6">
              <Link 
                href={link}
                className="inline-flex items-center text-primary-600 font-semibold hover:text-primary-700 transition-colors group"
              >
                <span>D√©couvrir ce service</span>
                <motion.div
                  animate={{ x: [0, 4, 0] }}
                  transition={{ duration: 1.5, repeat: Infinity }}
                >
                  <ArrowRight className="w-4 h-4 ml-2" />
                </motion.div>
              </Link>
            </div>
          </div>
        </div>
      </motion.div>
    );
  }
  
  // Default variant
  return (
    <motion.div
      whileHover={{ scale: 1.02, translateY: -6 }}
      transition={{ type: "spring", stiffness: 300, damping: 15 }}
      className="group h-full"
    >
      <div className="bg-white rounded-2xl border border-gray-200 overflow-hidden hover:shadow-2xl transition-shadow duration-300 h-full flex flex-col">
        {/* Icon Header */}
        <motion.div
          className={cn(
            "p-8 border-b bg-gradient-to-br",
            colorClass.includes('blue') && "from-blue-50 to-blue-100",
            colorClass.includes('purple') && "from-purple-50 to-purple-100"
          )}
          whileHover={{ scale: 1.05 }}
        >
          <div className="flex items-center justify-between">
            <motion.div 
              className="text-4xl"
              whileHover={{ scale: 1.3, rotate: 20 }}
            >
              {service.icon}
            </motion.div>
            <motion.div
              animate={{ x: [0, 6, 0] }}
              transition={{ duration: 2.5, repeat: Infinity }}
            >
              <ArrowRight className="w-6 h-6 text-primary-600" />
            </motion.div>
          </div>
        </motion.div>
        
        {/* Content */}
        <div className="p-8 flex-grow">
          <h3 className="text-xl font-bold text-gray-900 mb-3 group-hover:text-primary-600 transition-colors">
            {service.title}
          </h3>
          <p className="text-gray-600 mb-6">
            {service.description}
          </p>
          
          {service.features && service.features.length > 0 && (
            <ul className="space-y-3 mb-8">
              {service.features.slice(0, 4).map((feature, index) => (
                <motion.li
                  key={index}
                  className="flex items-center text-sm text-gray-700"
                  initial={{ opacity: 0, x: -20 }}
                  whileInView={{ opacity: 1, x: 0 }}
                  transition={{ delay: index * 0.08 }}
                >
                  <svg className="w-4 h-4 mr-3 text-green-500 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                  </svg>
                  {feature}
                </motion.li>
              ))}
            </ul>
          )}
        </div>
        
        {/* Footer */}
        <div className="p-8 pt-0">
          <Link 
            href={link}
            className="inline-flex items-center text-primary-600 font-semibold hover:text-primary-700 transition-colors group"
          >
            <span>Explorer ce service</span>
            <motion.div
              animate={{ x: [0, 4, 0] }}
              transition={{ duration: 1.5, repeat: Infinity }}
            >
              <ArrowRight className="w-4 h-4 ml-2" />
            </motion.div>
          </Link>
        </div>
      </div>
    </motion.div>
  );
};

export default ServiceCard;